@import url(../node_modules/fontisto/css/fontisto/fontisto.min.css);
.header {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column; }
  .header .theme-btn {
    cursor: pointer;
    width: 80px;
    height: 35px;
    align-self: flex-end;
    background: #bce0fd;
    border-radius: 25px;
    display: flex; }
    .header .theme-btn.dark {
      background: #2699fb; }
    .header .theme-btn:hover .ball {
      background: rgba(38, 153, 251, 0.5) !important; }
      .header .theme-btn:hover .ball.dark {
        background: rgba(255, 255, 255, 0.5) !important; }
    .header .theme-btn .button {
      color: #ffffff;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 5px; }
      .header .theme-btn .button .text {
        right: 5px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: 0.75rem; }
        .header .theme-btn .button .text.active {
          right: 100%;
          margin-right: -42.59524px; }
      .header .theme-btn .button .ball {
        position: absolute;
        top: 50%;
        right: 100%;
        margin-right: -32.59524px;
        transform: translateY(-50%);
        width: 27.59524px;
        height: 27.59524px;
        background: white;
        border-radius: 50%; }
        .header .theme-btn .button .ball.active {
          right: 5px;
          margin-right: 0; }
  .header .heading {
    display: flex;
    align-items: center;
    width: 100%; }
    .header .heading .news {
      color: #2699fb;
      margin-right: 1rem; }
      .header .heading .news.dark {
        color: #ffffff; }
    .header .heading .line {
      height: 2px;
      width: 100%;
      background: #f1f9ff; }

.posts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 1rem 0; }

.post {
  width: calc(50% - 2rem);
  padding: 1rem;
  cursor: pointer; }
  @media (max-width: 1024px) {
    .post {
      width: auto;
      margin-left: 0 !important;
      margin-right: 0 !important; } }
  .post:hover {
    background: rgba(38, 153, 251, 0.1); }
  .post:nth-child(odd) {
    margin-right: 1rem; }
  .post:nth-child(even) {
    margin-left: 1rem; }
  .post.simple {
    width: 100%;
    margin: 0; }
  .post .img {
    color: #2699fb;
    overflow: hidden;
    height: 225px; }
    .post .img .likes {
      position: absolute;
      right: 3%;
      bottom: 5%;
      display: flex; }
      .post .img .likes .icon {
        margin-right: 5px; }
    .post .img img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%; }
  .post .body {
    padding: 1.5rem 0;
    padding-bottom: 0; }
    .post .body.dark .header,
    .post .body.dark .description,
    .post .body.dark .footer {
      color: #ffffff; }
      .post .body.dark .header i,
      .post .body.dark .description i,
      .post .body.dark .footer i {
        color: #2699fb; }
    .post .body .header {
      color: #2699fb;
      margin-bottom: 1rem; }
    .post .body .description {
      color: #2699fb;
      margin-bottom: 1.5rem; }
    .post .body .footer {
      display: flex;
      color: #2699fb;
      justify-content: space-between; }

.main-wrapper {
  display: flex;
  width: 100%; }
  .main-wrapper .main {
    width: 66.666%;
    padding: 1rem 2rem; }
    @media (max-width: 769px) {
      .main-wrapper .main {
        width: 100%; } }
    .main-wrapper .main.dark {
      background: #7fc4fd; }
  .main-wrapper .sidebar {
    width: 33.333%;
    height: 100vh;
    background: #f1f9ff;
    padding: 2.5%;
    overflow-y: scroll; }
    @media (max-width: 769px) {
      .main-wrapper .sidebar {
        display: none; } }
    .main-wrapper .sidebar.dark {
      background: #bce0fd; }

.icon-margin {
  margin-right: 5px; }

.tns-outer {
  overflow: hidden; }
  .tns-outer .tns-nav {
    width: 100%;
    display: flex;
    justify-content: center; }
    .tns-outer .tns-nav button {
      border: none;
      border-radius: 50%;
      background: #bce0fd;
      width: 20px;
      height: 20px;
      margin: 5px; }
      .tns-outer .tns-nav button.tns-nav-active {
        background: #2699fb; }
        .tns-outer .tns-nav button.tns-nav-active.dark {
          background: #f1f9ff; }
  .tns-outer .tns-liveregion {
    display: none; }

* {
  position: relative;
  box-sizing: border-box;
  font-family: "Arial";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  transition: 0.1s linear all, color 0s; }

[hidden] {
  display: none !important; }

body {
  width: 100%;
  max-width: 100vw;
  font-size: 1.2rem;
  overflow-x: hidden; }

.container {
  max-width: 1440px;
  margin: 0 auto; }
